import React from 'react';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import api from '../services/api';

export default class SelectRest extends React.Component {

    constructor(props) {
        super(props);

        this.label = React.createRef();

        this.state = {
            data: [],
            labelWidth: 100,
            value: ''
        }
    }

    componentDidMount = () => {

        let me = this;

        this.setState({ labelWidth: this.label.current.clientWidth });

        api.get(this.props.api, { data: {} }).then(result => { me.setState({ data: result.data }) });
    }

    handleChange = (event) => {
        this.setState({ value: event.target.value });
     
        this.props.handleChange(event);
    }

    render() {

        return (
            <div>
                <InputLabel id={this.props.name + 'label'} ref={this.label}>
                    {this.props.label}
                </InputLabel>
                <Select
                    className="reactSelect"
                    labelId={this.props.name + 'label'}
                    id={this.props.name + 'field'}
                    required
                    fullWidth
                    inputRef={this.props.inputRef}
                    error={this.props.error}
                    value={this.state.value}
                    labelWidth={this.state.labelWidth}
                    onChange={this.handleChange}
                >
                    {
                        this.state.data.map(function (item) {
                            return (<MenuItem key={item.id} value={item.id}>{item.descricao}</MenuItem>)
                        })
                    }
                </Select>
            </div>
        )
    }
}
