import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import FormControl from '@material-ui/core/FormControl';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import Documento from '../../components/documento'
import FormHelperText from "@material-ui/core/FormHelperText";
import Avatar from '@material-ui/core/Avatar';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import SelectRest from '../../components/select-rest';

const useStyles = makeStyles(theme => ({
  '@global': {
    body: {
      backgroundColor: theme.palette.common.white,
    },
  },
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%',
    marginTop: theme.spacing(3),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));


export default function Cadastro() {

  const [state, setState] = React.useState({
    estabelecimento: '',
    nome: '',
    documento: '',
    email: '',
    senha: ''
  });

  const [error, setError] = React.useState({
    estabelecimento: true,
    nome: true,
    documento: true,
    email: true,
    senha: true,
    mensagemestabelecimento: 'Campo obrigatório',
    mensagemnome: 'Campo obrigatório',
    mensagemdocumento: 'Campo obrigatório',
    mensagememail: 'Campo obrigatório',
    mensagemsenha: 'Campo obrigatório',
  });


  const classes = useStyles();

  const handleSubmit = event => {
    debugger
    event.preventDefault();
    console.log(event);
  };

  const options = [
    { value: '1', label: 'Cabelereiro' },
    { value: '2', label: 'Dentista' },
    { value: '3', label: 'Médico' }
  ]

  function handleChange(name, value, fieldError, mensagem) {

    setState({
      ...state,
      [name]: value,
    });

    setError({
      ...error,
      [name]: fieldError,
      ['mensagem' + name]: mensagem,
    });
  };

  const changeEstabelecimento = event => {
  
    let error = event.target.value === '';
    let mensagem = error ? 'Campo obrigatório' : '';

    handleChange('estabelecimento', event.target.value, error, mensagem);
  };

  const changeDocumento = event => {
    let error = event.target.value === '';
    let mensagem = error ? 'Campo obrigatório' : '';

    handleChange('documento', event.target.value, error, mensagem);
  };

  const changeNome = event => {
    let error = event.target.value === '';
    let mensagem = error ? 'Campo obrigatório' : '';

    handleChange('nome', event.target.value, error, mensagem);
  };
  const changeEmail = event => {
    let error = event.target.value === '';
    let mensagem = error ? 'Campo obrigatório' : '';

    handleChange('email', event.target.value, error, mensagem);
  };
  const changeSenha = event => {
    let error = event.target.value === '';
    let mensagem = error ? 'Campo obrigatório' : '';

    handleChange('senha', event.target.value, error, mensagem);
  };

  return (

    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Cadastro de Empresa
        </Typography>
        <Grid container spacing={2}>
          <form className={classes.form} noValidate onSubmit={handleSubmit}>
            <FormControl variant="outlined" className={classes.form}>
              <Grid container spacing={2}>
                <Grid item xs={12}>
                  <SelectRest
                    name='estamelecimento'
                    label='Estabelecimento'
                    value={state.estabelecimento}
                    error={error.estabelecimento}
                    handleChange={changeEstabelecimento}
                    api='estabelecimento'
                  />
                  {error.estabelecimento && <FormHelperText>{error.mensagemestabelecimento}</FormHelperText>}
                </Grid>
                <Grid item xs={12} >
                  <Documento
                    value={state.documento}
                    error={error.documento}
                    onChange={changeDocumento}
                  />
                  {error.documento && <FormHelperText>{error.mensagemdocumento}</FormHelperText>}
                </Grid>
                <Grid item xs={12} >
                  <TextField
                    variant="outlined"
                    name="nome"
                    required
                    fullWidth
                    id="nome"
                    label="Nome"
                    autoComplete="nome"
                    value={state.nome}
                    error={error.nome}
                    onChange={changeNome}
                  />
                  {error.nome && <FormHelperText>{error.mensagemnome}</FormHelperText>}
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    variant="outlined"
                    required
                    fullWidth
                    id="email"
                    label="Email"
                    name="email"
                    autoComplete="email"
                    value={state.email}
                    error={error.email}
                    onChange={changeEmail}
                  />
                  {error.email && <FormHelperText>{error.mensagememail}</FormHelperText>}
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    variant="outlined"
                    required
                    fullWidth
                    name="senha"
                    label="Senha"
                    type="password"
                    id="senha"
                    autoComplete="current-password"
                    value={state.senha}
                    error={error.senha}
                    onChange={changeSenha}
                  />
                  {error.senha && <FormHelperText>{error.mensagemsenha}</FormHelperText>}
                </Grid>
              </Grid>
              <Button
                type="submit"
                fullWidth
                variant="contained"
                color="primary"
                disabled={(error.estabelecimento || error.nome || error.documento || error.email || error.senha)}
                className={classes.submit}
              >
                Cadastrar
          </Button>
            </FormControl>
          </form>
        </Grid>
      </div>
    </Container>
  );
}