import NumberFormat from 'react-number-format';
import TextField from '@material-ui/core/TextField';
import React from 'react';

export default class Documento extends React.Component {

    constructor(props) {
        super(props);

        this.name = props.name;
    }

    formatWithPattern = (numStr, format) => {

        let returnValue = [];
        let arrFormat = format.split('');
        let count = 0;
        for (var i = 0; i < arrFormat.length; i++) {

            if (arrFormat[i] === '#') {

                returnValue[i] = numStr[count];
                count++;
            } else {
                returnValue[i] = format[i];
            }

            if (count === numStr.length) {
                break
            }
        }

        return returnValue.join('');
    }

    formatDocumento = (val) => {

        let value = val.replace('[^0-9]', '');

        if (value.length > 11) {

            return this.formatWithPattern(value, '##.###.###/####-##');
        }

        return this.formatWithPattern(value, '###.###.###-##');
    }

    render() {

        return (
            <NumberFormat
                customInput={TextField}
                format={this.formatDocumento}
                variant="outlined"
                required
                fullWidth
                value={this.props.documento}
                error={this.props.error}
                onChange={this.props.onChange}
                id="documento"
                label="CPF/CNPJ"
                name="documento"
                autoComplete="documento"
            />
        )
    }
}